.Mobile {
    text-align: center;
  }
  
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  
  body{
    background: #eee;
  }
  
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  .Buttons{
      width: 100vw;
  }
  button{
    width: 25vw;
    height: 25vw;
    max-width: 300px;
    max-height: 300px;
    border-radius: 5px;
    -webkit-box-shadow: 0 5px #999;
            box-shadow: 0 5px #999;
    margin: 5px
  }
  button:active {
    -webkit-box-shadow: 0 2px #666;
            box-shadow: 0 2px #666;
    -webkit-transform: translateY(4px);
        -ms-transform: translateY(4px);
            transform: translateY(4px);
  }
  
  .status{
    display: inline-block;
    padding: 15px 25px;
    font-size: 24px;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: rgb(206, 47, 47);
    border: none;
    border-radius: 15px;
    -webkit-box-shadow: 0 3px #999;
            box-shadow: 0 3px #999;
    width: 50vw;
    height: 20vw;
    border-radius: 5px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align:center;
    vertical-align: middle;
    line-height: 20vw;
  }


